// app/context/AuthContext.js
'use client';

// Keep React imports, remove useEffect from here if not used elsewhere
import React, { createContext, useState, useContext, useCallback, useEffect } from 'react';

// firebase imports
import { auth } from '.../../firebase/firebaseConfig'; // Adjust path
import { onAuthStateChanged, signOut } from 'firebase/auth'; // Import signOut too

const AUTH_STORAGE_KEY = 'authUser';

const AuthContext = createContext({
  user: null,
  loginUser: () => { console.warn('loginUser function called without AuthProvider'); },
  logoutUser: () => { console.warn('logoutUser function called without AuthProvider'); },
});

export const useAuth = () => {
  return context = useContext(AuthContext);
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isInitialized, setIsInitialized] = useState(false);
  
  // Added loading state
  const [loading, isLoading] = useState(false);

  // block to fetch user from localstorage to login
  useEffect(() => {
    try {
      const storedUser = window.localStorage.getItem(AUTH_STORAGE_KEY);
      if (storedUser) {
        setUser(storedUser); // Set state based on localStorage
      } else {
        console.log('Auth Context: No user found in localStorage');
      }
    } catch (error) {
      setUser(null);
    } finally {
        setIsInitialized(true);
    }
  }, []);

  // block to add item in localstorage if user logs in
  useEffect(() => {
      try {
        if (user) {
          window.localStorage.setItem(AUTH_STORAGE_KEY, user);
        } else {
          window.localStorage.removeItem(AUTH_STORAGE_KEY);
        }
      } catch (error) {
        console.error("Auth Context: Error writing to localStorage key");
      }
  }, [user, isInitialized]);

    // Listener for authentication state changes
    useEffect(() => {
      const unsubscribe = onAuthStateChanged(auth, (user) => {
          console.log("Auth state changed. User:", user ? user.uid : null);
          setCurrentUser(user); // Set user to the Firebase user object, or null if logged out
      });

      // Cleanup subscription on unmount
      return unsubscribe;
  }, []); // Run only once on mount

  // Login function remains the same - it just calls setUser
  const loginUser = useCallback((username) => {
    if (username) {
      setUser(username);
      console.log(`Auth Context: User logged in`);
    } else {
      console.warn('Auth Context: Attempted to login with empty username');
      setUser(null);
    }
  }, []);

  // Logout function remains the same - it just calls setUser
  const logoutUser = async () => {
    {/* added try catch block for logout */}
    try {
        await signOut(auth);
        // currentUser will be set to null by onAuthStateChanged
        console.log("Logout successful");
    } catch (error) {
        console.error("Logout failed:", error);
    }
    {/* End of try catch block */}
    
    setUser(null);
    console.log('Auth Context: User logged out');
  };

  // Include isInitialized in the context value if components need to wait for auth check
  const value = {
    user,
    isInitialized, // Components can use this to show loading states
    loginUser,
    logoutUser,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};